@import './zce-common-body.scss';
@import './zce-body.scss';

.zce-content-body {

    // style article-relate 
    .article-relate {
        .story {

            .row {
                display: block;
            }

            &__thumb {
                width: 33%;
                float: left;
                margin-right: 16px;
                height: 20vw;
                max-height: 112px;
            }

            &__heading a {
                font-weight: $font-weight-medium;
                font-size: 18px;
                line-height: 26px;
                color: #212529;
            }

            &__summary {
                margin-top: 10px;
                font-weight: $font-weight-normal;
                font-size: 15px;
                line-height: 23px;
                color: #6C757D;
            }
        }
    }

    // style related-topic
    .related-topic {
        padding: 20px;
        box-shadow: 1px 0px 50px rgba(0, 0, 0, 0.1);

        .title {
            font-weight: $font-weight-semibold;
            font-size: 20px;
            line-height: 30px;
            text-transform: uppercase;
            color: #212529;
            padding-bottom: 8px;
            border-bottom: 1px solid #DC3545;
            margin-bottom: 15px;

            &:hover {
                color: $primary !important;
            }
        }

        .story {
            margin-top: 16px;
            padding-top: 16px;
            border-top: 1px solid #E9ECEF;

            &:first-child {
                margin-top: 0;
                padding-top: 0;
                border-top: none;

                .story__thumb {
                    float: left;
                    margin-right: 15px;
                }
            }

            &__thumb {
                width: 120px;
                height: 80px;
            }

            &__heading a {
                font-weight: $font-weight-semibold;
                font-size: 17px;
                line-height: 25px;
                color: #343A40;
                padding: 0;



                &::before {
                    display: none;
                }
            }
        }

    }

    // style related-news
    .related-news {

        .title {
            font-size: 20px;
            line-height: 30px;
            text-transform: uppercase;
            color: #212529;
            background: url(../img/bottom-decor_v2.png) bottom left no-repeat;
            padding-bottom: 10px;
            margin-bottom: 20px;
        }

        .story {
            margin-top: 12px;

            &:first-child {
                margin-top: 0;
            }

            &__heading a {
                font-weight: $font-weight-medium;
                font-size: 17px;
                line-height: 25px;
                color: #343A40;
                position: relative;
                padding-left: 20px;

                &::before {
                    position: absolute;
                    content: "";
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background: #E9ECEF;
                    left: 0;
                    top: 9px;
                }
            }
        }

    }

    // style author
    .article__author {
        font-weight: $font-weight-medium;
        font-size: 17px;
        line-height: 25px;
        color: #343A40;
        text-align: right;
    }

}

// style tags
.article__tag {
    display: flex;
    align-items: center;

    .box-heading {
        display: none;
    }


    .box-content {
        display: flex;
        flex-wrap: wrap;

        >* {
            border: 1px solid #DEE2E6;
            border-radius: 8px;
            padding: 11px 8px;
            font-weight: 400;
            font-size: 15px;
            line-height: 8px;
            color: #212529;
            margin-left: 8px;

            &:hover {
                color: $primary;
            }

            &:first-child {
                margin-left: 0;
            }
        }
    }
}